@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Montserrat Alternates";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/MontserratAlternates-Regular.woff2") format("woff2"), url("../fonts/MontserratAlternates-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat Alternates";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/MontserratAlternates-Bold.woff2") format("woff2"), url("../fonts/MontserratAlternates-Bold.woff") format("woff");
  font-display: swap;
}
.page {
  font-family: "Montserrat Alternates", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #525453;
}
@media screen and (min-width: 768px) {
  .page {
    font-size: 16px;
  }
}

.header {
  background-image: url("../img/mob/banner-mob.png");
  background-color: #ffffff;
  height: auto;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  padding: 16% 6% 0;
  box-sizing: border-box;
  position: relative;
}
@media (min-resolution: 2dppx) {
  .header {
    background-image: url("../img/mob/banner-mob@2x.png");
    background-size: 432px auto;
    min-height: 198px;
  }
}
@media screen and (min-width: 768px) {
  .header {
    padding: 22% 6% 0;
  }
}
@media screen and (min-width: 768px) and (min-resolution: 2dppx) {
  .header {
    background-image: url("../img/mob/banner-mob@2x.png");
    background-size: auto;
    min-height: 398px;
  }
}

.header__btn {
  width: 40px;
  height: 50px;
  padding: 10px 0;
  background-color: transparent;
  border: none;
  display: block;
  margin-left: auto;
  margin-right: 10%;
  color: #ffffff;
  font-family: inherit;
  font-size: 11px;
}

.header__title {
  color: #ffffff;
  font-weight: inherit;
  margin-top: 40px;
  margin-left: 8%;
  font-size: 26px;
  line-height: 30px;
}
@media screen and (min-width: 768px) {
  .header__title {
    font-size: 33px;
  }
}

.header::after {
  content: "";
  position: absolute;
  width: 395px;
  height: 20px;
  background-image: url("../img/mob/Line1.svg");
  background-repeat: no-repeat;
  bottom: -1%;
  left: 50%;
  transform: translate(-50%, -13%);
}

.contact {
  width: 44%;
  font-size: 12px;
  display: flex;
  justify-content: end;
  align-items: end;
}

.contact__block > p {
  margin: 0;
}

.contact__tg {
  color: #525453;
}

.content__text {
  display: block;
}

.content__text:not(:last-child) {
  margin-bottom: 10px;
}

.footer {
  background-color: #FFEFEF;
  padding: 6px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__signature {
  width: 152px;
  font-size: 12px;
  position: relative;
}

.footer__signature::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  background-image: url("../img/mob/heart.png");
  background-repeat: no-repeat;
  top: 58%;
  right: 27%;
}

.form {
  position: relative;
  text-transform: uppercase;
  padding: 20px 60px;
}

.form::before {
  content: "";
  position: absolute;
  width: 280px;
  height: 30px;
  background-image: url("../img/mob/Line2.svg");
  background-repeat: no-repeat;
  top: -8%;
  left: 0;
}

.form::after {
  content: "";
  position: absolute;
  width: 280px;
  height: 30px;
  background-image: url("../img/mob/Line3.svg");
  background-repeat: no-repeat;
  bottom: -26%;
  right: 4%;
}

.form__btn {
  position: relative;
  background-color: #FFA8A8;
  display: block;
  width: 202px;
  height: 42px;
  color: #ffffff;
  padding: 10px 48px;
  font-size: 20px;
  margin-left: auto;
  text-decoration: none;
  border: 1px solid #ffa8a8;
  border-radius: 15px;
  box-shadow: 7px 7px 22px -6px rgb(82, 84, 83);
  -webkit-box-shadow: 7px 7px 22px -6px rgb(82, 84, 83);
  -moz-box-shadow: 7px 7px 22px -6px rgb(82, 84, 83);
}

.form__btn::before {
  content: "";
  position: absolute;
  width: 105px;
  height: 50px;
  background-image: url("../img/mob/tyk.png");
  background-repeat: no-repeat;
  top: -55px;
  right: -24px;
}

.form__btn:hover {
  width: 190px;
  height: 38px;
  background-color: #e79090;
  font-size: 16px;
}

.format {
  position: relative;
  padding: 76px 0 20px;
}

.format::before {
  content: "Какой формат?";
  position: absolute;
  color: #ffffff;
  padding: 10px 16px;
  font-size: 19px;
  font-weight: 400;
  width: 197px;
  height: 44px;
  background-image: url("../img/mob/title-brown.svg");
  background-repeat: no-repeat;
  top: 15%;
  left: 65%;
  transform: translate(-50%, -13%);
}

.format-block {
  display: flex;
  gap: 0 6%;
}

.format__content {
  width: 50%;
  font-size: 13px;
}

.gift {
  padding: 0 20px 30px;
}

.gift__title {
  font-weight: 400;
  background-image: url("../img/mob/border-pink.svg");
  background-repeat: no-repeat;
  width: 228px;
  height: 68px;
  padding: 20px 55px;
  margin-top: 0;
  margin-left: 15%;
}

.howto {
  display: flex;
  padding: 0 20px;
  gap: 0 12%;
}

.howto__item {
  display: flex;
  align-items: center;
  gap: 0 4px;
}

.imgs {
  position: relative;
}

.imgs::before {
  content: "";
  position: absolute;
  width: 1px;
  height: 300px;
  background-color: #525453;
  top: 21%;
  left: 38%;
  transform: translate(-50%, -13%);
}

.imgs__block {
  display: flex;
  padding: 10px 20px 10px 46px;
  gap: 0 18%;
}

.imgs__item--no {
  width: 68px;
  height: auto;
}

.imgs__item--y {
  width: 200px;
  height: auto;
}

.intro {
  padding: 30px 0;
  display: flex;
  gap: 0 8%;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .intro {
    justify-content: center;
  }
}

.intro__text {
  min-width: 152px;
  width: 40%;
}
@media screen and (min-width: 768px) {
  .intro__text {
    width: 30%;
  }
}

.intro__img {
  height: auto;
}
@media screen and (min-width: 768px) {
  .intro__img {
    width: 220px;
  }
}

.main {
  max-width: 450px;
  width: 100%;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .main {
    max-width: 1000px;
  }
}

.wear {
  position: relative;
  padding: 50px 0 20px;
}

.wear::before {
  content: "Мне нечего надеть";
  position: absolute;
  color: #525453;
  padding: 11px 12px;
  font-size: 16px;
  font-weight: 400;
  width: 195px;
  height: 42px;
  background-image: url("../img/mob/title-pink.svg");
  background-repeat: no-repeat;
  top: 2%;
  left: 30%;
  transform: translate(-50%, -13%);
}

.wear__content {
  padding: 0 20px;
}

.when {
  position: relative;
  padding: 50px 0 20px;
  margin-bottom: 28px;
}

.when::after {
  content: "Когда?";
  position: absolute;
  color: #ffffff;
  padding: 6px 52px;
  font-size: 24px;
  font-weight: 400;
  width: 195px;
  height: 42px;
  background-image: url("../img/mob/title-brown.svg");
  background-repeat: no-repeat;
  top: -1%;
  left: 50%;
  transform: translate(-50%, -13%);
}

.when__text {
  text-align: center;
  position: relative;
}
@media screen and (min-width: 768px) {
  .when__text {
    font-size: 14.5px;
  }
}

.when__text:after {
  content: "";
  position: absolute;
  width: 228px;
  height: 92px;
  background-image: url("../img/mob/border-pink.svg");
  background-repeat: no-repeat;
  top: -1%;
  left: 50%;
  transform: translate(-50%, -13%);
}
@media screen and (min-width: 1200px) {
  .when__text:after {
    width: 630px;
    background-image: url("../img/border-pink-desk.svg");
  }
}

.where {
  position: relative;
  padding: 50px 0 20px;
  display: flex;
}
@media screen and (min-width: 768px) {
  .where {
    padding: 50px 80px 20px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
  }
}

.where::after {
  content: "Где?";
  position: absolute;
  color: #525453;
  padding: 6px 70px;
  font-size: 24px;
  font-weight: 400;
  width: 195px;
  height: 42px;
  background-image: url("../img/mob/title-pink.svg");
  background-repeat: no-repeat;
  top: -1%;
  left: 30%;
  transform: translate(-50%, -13%);
}

.where__text {
  text-align: center;
  position: relative;
}

.where__content {
  padding: 0 20px;
}
@media screen and (min-width: 768px) {
  .where__content {
    font-size: 14px;
    width: 50%;
  }
}

.where__content-name {
  display: block;
  margin-bottom: 10px;
}

.where__map {
  width: 200px;
  height: 202px;
}

.where__text:after {
  content: "";
  position: absolute;
  width: 228px;
  height: 92px;
  background-image: url("../img/mob/border-pink.svg");
  background-repeat: no-repeat;
  top: -1%;
  left: 50%;
  transform: translate(-50%, -13%);
}

.where__content-info {
  font-size: 12px;
}
@media screen and (min-width: 768px) {
  .where__content-info {
    font-size: 14px;
  }
}

@media screen and (min-width: 768px) {
  .where__map {
    width: 380px;
    height: 180px;
  }
}